/*--------------------------------------------------------------
2. File Attachment (.downloads)
--------------------------------------------------------------*/
.downloads {
  position: relative;
  margin: 3rem 0;
  padding-top: 3rem; }
  .downloads:before {
    content: '';
    height: 1px;
    width: 100%;
    max-width: 16rem;
    display: block;
    position: absolute;
    top: 0;
    background-color: #d0d0d0; }
  .downloads img {
    margin-right: 1.5rem; }
  .downloads img.attachment-icon {
    padding: 1.5rem;
    border: 1px solid #e8e8e8; }
  .downloads .media-body p:first-child {
    font-weight: 700; }

