/*--------------------------------------------------------------
# Inner Circle Calendar Grid  & Layout

1. Layout
2. Calendar Grid styles
3. Event Preview/Details
--------------------------------------------------------------*/
#calendar-wrapper.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto;
  grid-row-gap: 2rem; }

#calendar {
  grid-row: 1; }

#event-preview {
  display: none; }

@media (min-width: calc(768px)) {
  #event-preview {
    display: block;
    grid-row: 2; } }

@media (min-width: calc(992px)) {
  #calendar-wrapper.grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 84px 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px; }
  #calendar {
    grid-row: 1 / span 2;
    grid-column: 1; }
  #event-preview {
    display: block;
    grid-row: 2;
    grid-column: 2; } }

